import React, { useState } from 'react';
import { PayPalButtons, usePayPalScriptReducer } from '@paypal/react-paypal-js';
import '../style/Paypal.scss';
import { Spinner } from 'react-bootstrap';

/**
 * 
 * isResolved
 * isRejected
 * isInitial
 */


const MyPaypal = (props) => {

    const [{ isPending }] = usePayPalScriptReducer();
    //const [{ options }, dispatch] = usePayPalScriptReducer();
    const { formation, horaire } = props;
    const [infoProduit] = useState(`${formation.titre} (${horaire})`);


    return (
        <div className='paypalBtn'>
            {!isPending ? (
                <PayPalButtons
                    createOrder={(data, actions) => {
                        console.log(actions);

                        return actions.order.create({
                            purchase_units: [
                                {
                                    amount: {
                                        value: formation.prix,
                                    },
                                    description: infoProduit
                                },
                            ]
                        });
                    }}

                    onApprove={async (data, actions) => {
                        const details = await actions.order.capture();
                        const name = details.payer.name.given_name;
                        alert(`Transaction completed by ${name}`);
                        horaire === 'jour' && formation.nbPlacesRestante.journée > 0 && formation.nbPlacesRestante.journée--;
                        horaire === 'soir' && formation.nbPlacesRestante.soirée > 0 && formation.nbPlacesRestante.soirée--;
                    }}

                    onCancel={async (data, actions) => {
                        alert(`Transaction canceled !`);
                    }}
                    onError={async (err) => {
                        console.log(err);
                    }}

                    style={{ layout: "vertical", color: "gold", shape: "pill" }} />
            ) : (

                <Spinner animation="border" role="status">
                    <span className="visually-hidden">Loading...</span>
                </Spinner>
            )}

        </div>
    );
};

export default MyPaypal;