import React, { useEffect, useState } from 'react';
import { Card, Container, Form, Modal } from 'react-bootstrap';
import '../style/MyCard.scss'
import '../style/CarousselItem.scss';
import { euro } from '../database/DataApi';
import { useRef } from 'react';
import MyPaypal from './MyPaypal';

const MyCard = (props) => {
    const { image, title, text, price } = props;
    const [currentFormation, setCurrentFormation] = useState(props.currentFormation);
    const [show, setShow] = useState(false);
    const horaireJour = useRef();
    const horaireSoir = useRef();
    const nbPlaces = currentFormation.nbPlacesRestante.journée + currentFormation.nbPlacesRestante.soirée;

    useEffect(() => {
        setCurrentFormation(props.currentFormation);
    }, [props]);

   
    return (
        <Container>
            <Card className='card' >
                <Card.Img variant="top" src={image} />
                <Card.Body>
                    <Card.Title>{title}</Card.Title>
                    <h6>
                        <Card.Text>{text}</Card.Text>
                    </h6>
                    <Card.Title className='mb-3'> <strong>{price}</strong> </Card.Title>
                    <button disabled={nbPlaces <= 0} onClick={() => setShow(true)} className={nbPlaces > 0 ? 'button' : 'buttonDisabled'}>Détails</button>
                    <Card.Text>
                        {
                            nbPlaces > 0 ? (
                                <em style={nbPlaces > 10 ? { color: 'green' } : { color: 'red' }}>
                                    {nbPlaces} places disponibles !
                                </em>
                            ) : (
                                <em style={{ color: 'red' }}>
                                    Complet !
                                </em>
                            )
                        }
                    </Card.Text>
                </Card.Body>
            </Card>



            {/************  DETAILS *************/}

            <Modal show={show} fullscreen={true} onHide={() => setShow(false)}>
                <Modal.Header closeButton>
                    <Modal.Title>Acheter la formation</Modal.Title>
                </Modal.Header>
                <Modal.Body className="mt-4">
                    <div className="mod">
                        <div className="details" id="titre">
                            <h2> {currentFormation.titre} </h2>
                        </div>
                        <div className="details" id="description">
                            <div>
                                <h6>{currentFormation.description}</h6>
                            </div>
                        </div>
                        <div className="details" >
                            <h1 id="prix"> <strong>{currentFormation.prix} {euro}</strong></h1>
                        </div>
                        <Card style={{ width: '35%' }} className='card mb-4' sm={1}>
                            <Card.Img variant="top" src={currentFormation.image} />
                            <div style={{ textAlign: 'left' }} className='p-2'>
                                <ul>
                                    <li>Date de début : <span><strong> {currentFormation.dateDebut} </strong></span></li>
                                    <li>Durée : <strong>{currentFormation.durée}</strong></li>
                                    <li>Formateur : {currentFormation.formateur}</li>
                                </ul>
                            </div>
                            <button className='button'>Table de matière</button>
                        </Card>
                    </div>

                    <h2 style={{ textAlign: 'center' }} className="details">Choisir l'horaire (<em >Tous les dimanches</em>) :</h2>
                    <div className="horaire">
                        <div className="details">
                            <Form.Check
                                disabled={currentFormation.nbPlacesRestante.journée > 0 ? false : true}
                                defaultChecked={currentFormation.nbPlacesRestante.journée > 0 ? true : false}
                                type={'radio'}
                                label={`De 10h00 à 14h00`}
                                name='jour'
                                ref={horaireJour}
                            />
                            <em
                                style={currentFormation.nbPlacesRestante.journée > 5 ? { color: 'green' } : { color: 'red' }}>
                                ({currentFormation.nbPlacesRestante.journée} places disponibles)
                            </em>
                        </div>
                        <div className="details">
                            <Form.Check
                                disabled={currentFormation.nbPlacesRestante.soirée > 0 ? false : true}
                                type={'radio'}
                                label={`De 20h00 à 23h59`}
                                name='jour'
                                ref={horaireSoir}
                            />
                            <em
                                style={currentFormation.nbPlacesRestante.soirée > 5 ? { color: 'green' } : { color: 'red' }}>
                                ({currentFormation.nbPlacesRestante.soirée} places disponibles)
                            </em>
                        </div>
                    </div>
                    <hr />

                    <MyPaypal formation={currentFormation} horaire={horaireJour.current ? 'jour' : 'soir'} />
                </Modal.Body>
            </Modal>
        </Container>
    );
};

export default MyCard;

//100 €