import React, { useState } from 'react';
import { useEffect } from 'react';
import { FloatingLabel, Form } from 'react-bootstrap';
import '../style/FormContact.scss';
import '../style/CarousselItem.scss';
import AOS from 'aos';

const FormContact = () => {
    const [validated, setValidated] = useState(false);

    useEffect(() => {
        AOS.refresh();
    }, []);

    const handleSubmit = (event) => {
        const form = event.currentTarget;
        if (form.checkValidity() === false) {
          event.preventDefault();
          event.stopPropagation();
        }
    
        setValidated(true);
      };


    return (
        <div className='form mb-4'>
            <Form data-aos="fade-up" noValidate validated={validated} onSubmit={handleSubmit}>
                <FloatingLabel

                    data-aos="slide-right"
                    controlId="floatingInput"
                    label="Nom et Prénom"
                    className="mb-3 formControl"
                >
                    <Form.Control required type="text" placeholder="Vially Dag" />
                    <Form.Control.Feedback type='invalid'>Looks good!</Form.Control.Feedback>
                </FloatingLabel>
                <FloatingLabel
                    data-aos="slide-left"
                    controlId="floatingInput"
                    label="Adresse e-mail"
                    className="mb-3 formControl"
                >
                
                    <Form.Control type="email" placeholder="name@example.com" />
                </FloatingLabel>
                <FloatingLabel
                    data-aos="slide-right"
                    controlId="floatingInput"
                    label="Numéro de téléphone"
                    className="mb-3 formControl"
                >
                    <Form.Control required type="text" placeholder="+32492473290" />
                </FloatingLabel>
                <FloatingLabel
                    data-aos="slide-left"
                    controlId="floatingInput"
                    label="Motivation"
                    className="mb-3 formControl"
                >
                    <Form.Control
                        required
                        as="textarea"
                        placeholder="Tes motivations"
                        style={{ height: '100px', resize: 'none' }}
                    />
                </FloatingLabel>
                <div data-aos="slide-right" type="submit" className='button'>Postuler</div>
            
            </Form>
        </div>
    );

};

export default FormContact;